
















































































import {Component, Prop, Vue} from 'vue-property-decorator';
import './WalletCreated.less'
import {NetworkType, UInt64, Crypto} from "nem2-sdk";
import {MnemonicPassPhrase, ExtendedKey, Wallet} from 'nem2-hd-wallets';
import {localRead, localSave} from '../../../utils/util';
import {strToHexCharCode} from '../../../utils/tools';
import {walletInterface} from "../../../interface/sdkWallet";
import Message from "@/message/Message";
import {createAccount, randomMnemonicWord} from "../../../utils/mnemonicUtil";
import {encryptKey, getAccountDefault, saveLocalWallet} from "../../../utils/appUtil";

@Component({
    components: {},
})
export default class WalletCreated extends Vue{
    @Prop({default:{}})
    createForm: any

    tags = 0
    formItem = {
        currentNetType: '',
        walletName: '',
        password: '',
        checkPW: '',
    }
    netType = [
        {
            value: NetworkType.MIJIN_TEST,
            label: 'MIJIN_TEST'
        }, {
            value: NetworkType.MAIN_NET,
            label: 'MAIN_NET'
        }, {
            value: NetworkType.TEST_NET,
            label: 'TEST_NET'
        }, {
            value: NetworkType.MIJIN,
            label: 'MIJIN'
        },
    ]
    showCover = true
    mnemonicRandomArr = []
    mosaics = []
    storeWallet = {}

    get mnemonic () {
        const mnemonic = this.$store.state.app.mnemonic
        return mnemonic['split'](' ')
    }

    get formInfo () {
        return this.createForm;
    }

    get node () {
        return this.$store.state.account.node
    }

    get walletList () {
        return this.$store.state.app.walletList
    }

    get currentXEM1() {
        return this.$store.state.account.currentXEM1
    }

    get currentXEM2() {
        return this.$store.state.account.currentXEM2
    }

    hideCover () {
        this.showCover = false
    }

    sureWord (index) {
        const word = this.mnemonicRandomArr[index]
        const wordSpan = document.createElement('span');
        wordSpan.innerText = word;
        wordSpan.onclick = () => {
            this.$refs['mnemonicWordDiv']['removeChild'](wordSpan)
        }
        this.$refs['mnemonicWordDiv']['append'](wordSpan)
    }

    checkMnemonic () {
        const mnemonicDiv = this.$refs['mnemonicWordDiv'];
        const mnemonicDivChild = mnemonicDiv['getElementsByTagName']('span');
        let childWord = []
        for(let i in mnemonicDivChild){
            if( typeof mnemonicDivChild[i] !== "object") continue;
            childWord.push(mnemonicDivChild[i]['innerText'])
        }
        if (JSON.stringify(childWord) != JSON.stringify(this.mnemonic)) {
            if (childWord.length < 1) {
                this.$Notice.warning({title:''+this.$t(Message.PLEASE_ENTER_MNEMONIC_INFO)})
            } else {
                this.$Notice.warning({title:''+this.$t(Message.MNEMONIC_INCONSISTENCY_ERROR)})
            }
            return false
        }
        return true
    }

    changeTabs (index) {
        switch (index) {
            case 0:
                this.tags = index
                break;
            case 1:
                this.mnemonicRandomArr = randomMnemonicWord(this.mnemonic)
                this.tags = index
                break;
            case 2:
                if(!this.checkMnemonic()){
                    return
                }
                const account = this.createdAccount()
                this.loginWallet(account)
                this.tags = index
                break;
        }
    }

    skipInput (index) {
        const account = this.createdAccount()
        this.loginWallet(account)
        this.tags = index
    }

    createdAccount () {
        const account = createAccount(this.mnemonic.join(' '))
        this.$store.commit('SET_ACCOUNT',account);
        return account
    }

    async loginWallet (account) {
        const that = this
        const walletName:any = this.formInfo['walletName'];
        const netType:NetworkType = Number(this.formInfo['currentNetType'])
        const walletList = this.$store.state.app.walletList
        const style = 'walletItem_bg_' + walletList.length % 3
        this.storeWallet = await getAccountDefault(walletName, account, netType, this.node,this.currentXEM1, this.currentXEM2)
        this.storeWallet['style'] = style
        const encryptObj = encryptKey(this.storeWallet['privateKey'], that.formInfo['password'])
        const mnemonicEnCodeObj = encryptKey(strToHexCharCode(this.mnemonic.join(' ')), that.formInfo['password'])
        const storeWallet = saveLocalWallet(this.storeWallet, encryptObj, null, mnemonicEnCodeObj)
        that.$store.commit('SET_WALLET', storeWallet)
    }

    toWalletPage () {
        this.$store.commit('SET_HAS_WALLET',true)
        this.$emit('toWalletDetails')
    }

    toBack () {
        this.$emit('closeCreated')
    }
}
